import { useRef, useEffect } from 'react'

export default () => {
  const ref = useRef()

  useEffect(() => {
    if (ref.current !== undefined) {
        const context2d = ref.current.getContext('2d')
        context2d.beginPath()
        context2d.moveTo(50, 50)
        context2d.lineTo(100, 100)
        context2d.closePath()
        context2d.stroke()
    }
  })

  return <div>
    Hello world

    <canvas 
        ref={(domElement) => ref.current = domElement}
        width={1000} 
        height={1000} 
        style={{width: 500, height: 500}} />
  </div>
}